 .controle:hover{
   text-decoration: none;
    color: #C22D2D;
    font-size: 1.4rem;
    border-radius: 4px;
    border: 2px solid brown;
    padding: 4px;
    cursor: pointer;
    transition: .5s;   
 }
 .controle{
   color: rgb(255, 255, 255);
   font-size: 1.3rem;
   border-radius: 4px;
   border: 2px solid rgb(163, 124, 124);
   padding: 6px;
   background-color: #C22D2D;
 }
 @media (min-width:200px){
  .controle:hover{
      font-size: 1rem;
      border-radius: 2px;
      padding: 2px;     
   }
   .controle{
     font-size: .9rem;
     padding: 1px;
   }
}
@media (min-width:480px){
  .controle:hover{
    font-size: 1.1rem;
    border-radius: 2px;
    padding: 2px;   
 }
 .controle{
   font-size: 1rem;
   padding: 1px;
 }
}
@media (min-width:768px){
  .controle:hover{
    font-size: 1.2rem;
    border-radius: 2px;
    padding: 2px;   
 }
 .controle{
   font-size: 1.1rem;
   padding: 1px;
 }
}
@media (min-width:1024px){
  .controle:hover{
    font-size: 1.4rem;
    border-radius: 2px;
    padding: 2px;   
 }
 .controle{
   font-size: 1.3rem;
   padding: 1px;
 }
}

 