
.ini video{
    width: 95%;
    margin: auto;
}
hr{
    margin-bottom: 5rem;
}
.cards{
    display: flex;
}
.cards h2{
    font-size: 2.5rem;
    font-weight: 400;
    color: rgb(70, 56, 56);
}
.card{
    display: inline-block;
    margin-top: 5%;
    width: 30%;
    padding: 1.5rem;
    margin: 1%;
    border-radius: 10px;
    border: 1px solid #C22D2D;
}
.card:hover{
    background-color: #c22d2d2a;
    padding: 2rem;
}
.card2{
    display: inline-block;
    margin-top: 5%;
    width: 30%;
    padding: 1.5rem;
    margin: 1%;
    border-radius: 10px;
    border: 1px solid #C22D2D;
}
.card2:hover{
    background-color: #c22d2d2a;
    padding: 2rem;
}
.img{
    display: inline-block;
    margin-top: 5%;
    width: 30%;
    padding: 1.5rem;
    margin: 1%;
}
.img:hover{
    width: 35%;
    padding: 0px;
    margin: 0px;
}
.img img{
    width: 100%;
}
.apres{
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: space-around;
    align-items: center;
}
.apres .tex{
    width: 49%;
    text-align: center;
    padding: 10px;
}
.apres .ima{
    width: 49%;
    text-align: center;
    padding: 10px;
    
}
.apres .ima img{
    width: 99%;
    border-radius: 10px;
}
.mais{
    display: none;
}
.ma{
    display: block;
    color: black;
}
h6{
    width: 55%;
    margin: auto;
    font-size: 1rem;
    margin-top: 1rem;
    background-color: #C22D2D;
    color: aliceblue;
    padding: .3rem;
    border-radius: 3px;
     cursor: pointer;
}
@media (min-width:200px){
    .inic img{
        background-size: cover;
        width: 100%
    }
    .ini video{
        display: none;
        width: 85%;
    }
    hr{
        margin-bottom: 1rem;
    }
    .cards h2{
        font-size: 1.1rem;
    }
    .card{
        margin-top: 3%;
        width: 48%;
        padding: .3rem;
        margin: .3%;
        border-radius: 10px;
    }
    .card h3{
     font-size: 1rem;
    }
    .card p{
     font-size: .9rem;
    }
    .card3{
        margin-top: 3%;
        width: 100%;
        padding: .3rem;
        margin: .3%;
        border-radius: 10px;
    }
    .card3 h3{
     font-size: 1.3rem;
    }
    .card3 p{
     font-size: .8rem;
    }
    .card2{
       display: none;
    }
    .card:hover{
        padding: .6rem;
    }
    .img{
        display: inline-block;
        margin-top: 5%;
        width: 31%;
        padding: .5rem;
        margin: .5%;
    }
    .img:hover{
        width: 35%;
        padding: 0px;
        margin: 0px;
    }
    .img img{
        width: 100%;
    }
    .apres{
        display: block;
    }
    .apres .tex{
        width: 99%;
        text-align: center;
        padding: 3px;
    }
    .apres .ima{
        width: 99%;
        text-align: center;
        padding: 3px;
        
    }
    .apres .ima img{
        width: 100%;
        border-radius: 10px;
    }
}
