.ma h2{
    font-size: 2.3rem;
}
.ma h3{
    font-size: 1.8rem;
}
.ma p{
    font-size: 1.1rem;
    padding: 4px 2.5rem;
}
.ima{
    display: flex;
    justify-content: space-around;
}
.ima img{
    width:80%;
    background-size: contain;
}
.im{
     display: flex;
    justify-content: space-around;
}
.im img{
    width:85%;
    background-size: contain;
    margin-bottom: 1rem;
}
@media (min-width:200px){
    .ma h3{
        font-size: 1.4rem;
    }
    .ima img{
        width:92%;
        background-size: contain;
        margin-bottom: 1rem;
    }
    .im{
        margin-bottom: 1rem;
    }
    .im img{
        width:95%;
    }
    .nav{
        margin-right: 1px;
    }
    .nav a{
        padding: .2rem .3rem;
        margin-top: .4rem ;
        margin-left: .4rem;
        border-radius: 3px;
        font-size: .9rem;
    }
}
@media (min-width:480px){
    .ma h3{
        font-size: 1.8rem;
    }
    .nav{
        margin-right: 2px;
    }
    .nav a{
        padding: .2rem .4rem;
        margin-top: .5rem ;
        margin-left: .5rem;
        border-radius: 3px;
        font-size: 1rem;
    }
}
@media (min-width:768px){
    .ma h3{
        font-size: 2rem;
    }
    .nav{
        margin-right: 2px;
    }
    .nav a{
        padding: .2rem .6rem;
        margin-top: .6rem ;
        margin-left: .6rem;
        border-radius: 3px;
        font-size: 1.2rem;
    }
}
@media (min-width:1024px){
    .ma h3{
        font-size: 2.3rem;
    }
    .nav{
        margin-right: 5px;
    }
    .nav a{
        padding: .2rem .6rem;
        margin-top: .6rem ;
        margin-left: .6rem;
        border-radius: 3px;
        font-size: 1.5rem;
    }
}