@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2? family= Playfair+Display:ital,wght@1,400;1,500 & display=swap');
@import url('https://fonts.googleapis.com/css2? family= Lora:ital@1 & display=swap');

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
:root{
    color: #F2F2F2;
    color: #222222;
    color: #333333;
    color: #ffffff;
    color: #C22D2D;


font-family: 'Dancing Script', cursiva;
font-family: 'Ysabeau', sans-serif;
}
html, body, #root{
    text-align: center;
    background-color: #F2F2F2;
}
h1{
    color: #C22D2D;
    font-family: 'Ysabeau', sans-serif;
    padding: 10px;
    margin: 5px 0px;
}
h2{
    color: rgb(70, 56, 56);
    font-weight: 300;
    font-family: 'Ysabeau', sans-serif;
    padding: 8px;
    margin: 4px 0px;
}
h3{
    color: #C22D2D;
    font-family: 'Ysabeau', sans-serif;
    padding: 6px;
    margin: 3px 0px;
}h4{
    color: #333333;
    font-family: 'Ysabeau', sans-serif;
    padding: 6px;
    margin: 3px 0px;
}
p{
    color: #222222;
    font-family: 'Ysabeau', sans-serif;
    padding: 4px;
    margin: 2px 0px;
    font-family: 'Lora', serifa;
    text-align: center;
}
@media (min-width:200px){
    h1{
        font-size: 1.2rem;
        padding: 4px;
        margin: 2px 0px;
    }
    h2{
        font-size: 1.1rem;
        padding: 3px;
        margin: 2px 0px;
    }
    h3{
        font-size: 1rem;
        padding: 3px;
        margin: 2px 0px;
    }h4{
        font-size: .9rem;
        padding: 2px;
        margin: 2px 0px;
    }
    p{
        font-size: .8rem;
        padding: 4px;
        margin: 2px 0px;
    }
}
@media (min-width:480px){
    h1{
        font-size: 2rem;
        padding: 5px;
        margin: 3px 0px;
    }
    h2{
        font-size: 1.7rem;
        padding: 3px;
        margin: 2px 0px;
    }
    h3{
        font-size: 1.5rem;
        padding: 3px;
        margin: 2px 0px;
    }h4{
        font-size: 1.3rem;
        padding: 2px;
        margin: 2px 0px;
    }
    p{
        font-size: .9rem;
        padding: 4px;
        margin: 2px 0px;
    } 
}
@media (min-width:768px){
    h1{
        font-size: 2.2rem;
        padding: 6px;
        margin: 4px 0px;
    }
    h2{
        font-size: 2rem;
        padding: 4px;
        margin: 4px 0px;
    }
    h3{
        font-size: 1.6rem;
        padding: 4px;
        margin: 4px 0px;
    }h4{
        font-size: 1.4rem;
        padding: 4px;
        margin: 4px 0px;
    }
    p{
        font-size: 1rem;
        padding: 4px;
        margin: 3px 0px;
    }
}
@media (min-width:1024px){
    h1{
        font-size: 2.8rem;
        padding:8px;
        margin: 6px 0px;
    }
    h2{
        font-size: 2.4rem;
        padding: 8px;
        margin: 6px 0px;
    }
    h3{
        font-size: 2rem;
        padding: 8px;
        margin: 6px 0px;
    }h4{
        font-size: 1.5rem;
        padding: 2px;
        margin: 2px 0px;
    }
    p{
        font-size: 1rem;
        padding: 4px;
        margin: 3px 0px;
    }
}