
.div{
    display: flex;
    gap: 5%;
    background-color: #C22D2D;
    justify-content: space-between
}
.div h1{
    font-size: 1.3rem;
    color:#fff;
    padding: 0px;
}
.div h3{
    font-size: 1.1rem;
    color:#fff;
    padding: 0px;
}
.div p{
    color:#e5f864;
    padding: 0px;
}
.div h4{
    font-size: 1rem;
    color: aliceblue;
    padding: 0px;
}
.div h5{
    font-size: 1.5rem;
}
.div h5:hover{
    font-size: 2.5rem;
}
.fa{
    color:#3b5998 ;
    border-radius: 50% ;  
}
.ins{
    color:#ffae00;
    border-radius: 50% ;  
}
.link{
    color: #0e76a8;
    border-radius: 50% ; 
}
.zap{
    color: #a4c639;
    border-radius: 50% ;  
}

.empre{
    width: 30%;
}
.conta{
    width: 40%;
    text-align: initial;
}
 hr{
    margin: 0px;
}
.info{
    display: flex;
    gap: 10%;
}
.infor{
    display: flex;
    gap: 27%;
}
.social{
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: aliceblue;
    width: 6%;
}
.roda{
    background-color: #C22D2D;
}
.roda span{
    color: #fff;
}
.roda a{
    color: #e5f864;
    font-size: 1.3rem;
    text-decoration: none;
    margin-left: 5px;
}
.roda a:hover{
    font-size: 1.4rem;
    color: #fcbb30;
    font-weight: bold;
}
/* ajuste de tela */
@media (min-width:200px){
    .div h1{
        font-size: 1.1rem;
        margin-right: .7rem;
    }
    .div h3{
        font-size: .9rem;
    }
    .div h4{
        font-size: .8rem;
    }
    .div h5{
        font-size: 1.5rem;
    }
    .div h5:hover{
        font-size: 1.7rem;
    }
    .empre{
       display: none;
    }
    .conta{
        width: 86%;
        
    }
    .social{
        width: 8%;
    }
    .roda a{
        font-size: .9rem;
        margin-left: 5px;
    }
    .roda a:hover{
        font-size: 1rem;
    }
}
@media (min-width:480px){
    .div h1{
        font-size: 1.2rem;
        margin-right: .7rem;
    }
    .div h3{
        font-size: 1rem;
    }
    .div h4{
        font-size: .9rem;
    }
    .div h5{
        font-size: 1.7rem;
    }
    .div h5:hover{
        font-size: 1.9rem;
    }
    .empre{
       display: none;
    }
    .conta{
        width: 86%;
        
    }
    .social{
        width: 10%;
    }
    .roda a{
        font-size: 1rem;
        margin-left: 5px;
    }
    .roda a:hover{
        font-size: 1.1rem;
    }
}
@media (min-width:768px){
    .div h1{
        font-size: 1.2rem;
    }
    .div h3{
        font-size: 1.1rem;
    }
    .div h4{
        font-size: 1rem;
    }
    .div h5{
        font-size: 2rem;
    }
    .div h5:hover{
        font-size: 2.1rem;
    }
    .empre{
        display: block;
        width: 40%;
    }
    .conta{
        width: 49%;
        
    }
    .social{
        width: 8%;
    }
    .roda a{
        font-size: 1.2rem;
        margin-left: 5px;
    }
    .roda a:hover{
        font-size: 1.3rem;
    }
}
@media (min-width:1024px){
    .div h1{
        font-size: 1.3rem;
    }
    .div h3{
        font-size: 1.2rem;
    }
    .div h4{
        font-size: 1.1rem;
    }
    .div h5{
        font-size: 2rem;
    }
    .div h5:hover{
        font-size: 2.1rem;
    }
    .empre{
        display: block;
        width: 40%;
    }
    .conta{
        width: 49%;
        
    }
    .social{
        width: 8%;
    }
    .roda a{
        font-size: 1.2rem;
        margin-left: 5px;
    }
    .roda a:hover{
        font-size: 1.3rem;
    }
}