
.a{
    text-decoration: none;
    color: rgb(255, 255, 255);
    font-size: 1.4rem;
    border-radius: 4px;
    background-color: #C22D2D;
    border: 2px solid rgb(163, 124, 124);
    padding: 3px;
    cursor: pointer;
    transition: .5s;
}
.a:hover{
    color: #C22D2D;
    font-size: 1.5rem;
    border-radius: 4px;
    border: 2px solid brown;
    background-color: #f8f8f8;
    padding: 4px;
}
@media (min-width:200px){
    .a{
        font-size: .9rem;
        border-radius: 5px;
        padding: 3px;
    }
    .a:hover{
        font-size: 1.1rem;
        padding: 4px;
    }
 }
 @media (min-width:480px){
    .a{
        font-size: 1rem;
        border-radius: 5px;
        padding: 1px;
    }
    .a:hover{
        font-size: 1.1rem;
        padding: 2px;
    } 
 }
 @media (min-width:768px){
    .a{
        font-size: 1.2rem;
        border-radius: 5px;
        padding: 3px;
    }
    .a:hover{
        font-size: 1.3rem;
        padding: 4px;
    }
 }
 @media (min-width:1024px){
    .a{
        font-size: 1.4rem;
        border-radius: 5px;
        padding: 4px;
    }
    .a:hover{
        font-size: 1.5rem;
        padding: 5px;
    }
 }