.controle{
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}
.controle label{
    margin-bottom: .6rem;
    font-weight: bold;
}
.cotrole textarea{
    padding: .7rem;
    border-radius: 0px;
    border: none;
    text-align: center;
}
.cotrole textarea::placeholder{
    color: rgba(0, 0, 0, 0.493);
}