.controle{
    display: flex;
    flex-direction: column;
    margin-bottom: .5rem;
}
.controle label{
    margin-bottom: .4rem;
    font-weight: bold;
}
.cotrole input{
    padding: .7rem;
    border-radius: 10px;
    border: none;
    text-align: center;
}
.cotrole input::placeholder{
    color: rgba(0, 0, 0, 0.493);
}