.back{
position: fixed;
top: 0;
left: 0;
bottom: 0;
right: 0;
background-color: rgba(0, 0, 0, 0.274);
z-index: 1000;
}
.back .cont{
    position: fixed;
    top: 1%;
    bottom: 1%;
    left:18%;
    right: 18%;
    background-color: rgba(240, 248, 255, 0.979);
    border-radius: 10px;
}
.back .cont img{
    width: 50%;
    background-size: cover;
}
.back .cont .fech{
    position: absolute;
    right: 2px;
    padding: .3rem;
    background-color: #f0f8ff00;
    font-size: 1.5rem;
}
.back .cont h3{
    font-size: 2rem;
    margin:0px;
    padding: 3px;
}
