
.centra{
    margin:auto;
    min-height: 65%;
    font-family: 'Ysabeau', sans-serif;
    background-image: linear-gradient(to bottom,  #c03131, #0e0000);
}
.centra h1{
    color: #c03131;
    font-family: 'Ysabeau', sans-serif;
    font-size: 3rem;
    font-weight: 800;
    margin: 0px;
    padding: 0px;
}
.centra h2{
    color:#c03131;
    font-family: 'Ysabeau', sans-serif;
    font-size: 2rem;
    font-weight: 500;
    margin: 0px;
    padding: 0px;
}
.centra h3{
    color:#758C20;
    font-family: 'Ysabeau', sans-serif;
    font-size: 1.2rem;
    font-weight: 500;
}
.centra h4{
    font-size: 1.1rem;
}
.centra label{
    font-weight: bold;
    color: #b62424;
    font-family: 'Ysabeau', sans-serif;
}
.centra button{
    width: 80%;
    color: #fff;
    margin-bottom: 10px;
    border: 2px solid #c75353;
    padding: .5rem;
    border-radius: 2rem;
    margin-bottom: 6rem;
}
.centra button:hover{
    background-color:#fff;
    color: #bd3232;
    border-radius: 2rem;
}
.cont{
margin: auto;
width: 65%;
display: flex;
flex-direction:column;
flex-wrap: nowrap;
align-content: center;
justify-content: space-between;
align-items: center;
gap: 20px;
background-color: #ffffff;
box-shadow: 5px 5px 5px 7px rgba(0, 0, 0, 0.466);
border-radius: 20px;
}
.cont div{
 width: 100%;
display: flex;
flex-direction:column;
flex-wrap: nowrap;
align-content: center;
justify-content: flex-start;
align-items: center;
}
.cont .form form{
    width: 100%;
}
.cont .form form span{
    font-size: 1rem;
    margin: 0px;
    padding: .5rem;
}
.cont .form form input{
    width: 75%;
    border-radius: 2rem;
    text-align: center;
    border: .2px solid rgba(0, 0, 0, 0.281);
}
.cont .form form textarea{
    width: 75%;
    border-radius: 10px;
}
.al{
 background-color: #b32323; 
 position: relative;
}
.al .detalhe{
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
    width: 95%;
    background-color: #fff2f2;
}
.detalhe div{
    width: 24%;
    height: 250px;
    border-radius: 2rem;
    margin: 3px;
    padding: 6px;
    margin-top: -3rem;
}
.detalhe div h1 svg{
  font-size: 3rem;
  background-color: #b32323;
  padding: .5rem;
  border-radius: 35%;
  cursor: pointer;
    color: #fff;
}
.detalhe div:hover{
    padding: 1.5rem;
    background-color: #c28080;
    color: #fff;
}
.detalhe div h4{
    color: #cc1818;
    text-shadow: #0e00006c;
}
.detalhe div p{
    font-size: .9rem;
    color: #cc1818;
    text-shadow: #0e00006c;
}
@media (min-width:200px){
    .centra h1{
        font-size: 1.3rem;
    }
    .centra h2{
        font-size: 1rem;
    }
    .centra h4{
        font-size: .9rem;
    }
    .centra button{
        width: 80%;
        padding: .1rem;
        margin-bottom: 3rem;
    }
    .cont{
    width: 97%;
    }
    .cont .form form span{
        font-size: .8rem;
        padding: .1rem;
    }
    .cont .form form input{
        width: 75%;
        padding: .4rem;
        border-radius: .5rem;
    }
    .cont .form form textarea{
        width: 75%;
        padding: .4rem;
        text-align: center;
        border-radius: 5px;
        margin-bottom: 0px;
    }
    .al .detalhe{
        display: flex;
        width: 98%;
    }
    .detalhe div{
        width: 49%;
        height: 150px;
        border-radius: 2rem;
    }
    .detalhe .ende{
        display: none;
    }
    .detalhe div h1 svg{
      font-size: 1.6rem;
      padding: .1rem;
    }
    .detalhe div:hover{
        padding: .5rem;
    }
    .detalhe div p{
        font-size: .7rem;
    }
}
@media (min-width:480px){
    .centra h1{
        font-size: 1.7rem;
    }
    .centra h2{
        font-size: 1.4rem;
    }
    .centra h4{
        font-size: 1rem;
    }
    .centra button{
        width: 70%;
        padding: .1rem;
        margin-bottom: 3rem;
    }
    .cont{
    width: 92%;
    }
    .cont .form form span{
        font-size: .9rem;
    }
    .cont .form form input{
        width: 75%;
        padding: .4rem;
    }
    .cont .form form textarea{
        width: 75%;
        padding: .4rem;
        text-align: center;
    }
    .al .detalhe{
        width: 95%;
    }
    .detalhe div{
        width: 49%;
        height: 150px;
    }
    .detalhe .ende{
        display: none;
    }
    .detalhe div h1 svg{
      font-size: 1.8rem;
      padding: .1rem;
    }
    .detalhe div:hover{
        padding: .5rem;
    }
    .detalhe div p{
        font-size: .8rem;
    }  
}
@media (min-width:768px){
    .centra h1{
        font-size: 2rem;
    }
    .centra h2{
        font-size: 1.7rem;
    }
    .centra h4{
        font-size: 1.1rem;
    }
    .centra button{
        width: 75%;
        padding: .1rem;
        margin-bottom: 3rem;
    }
    .cont{
    width: 80%;
    }
    .cont .form form span{
        font-size: .9rem;
    }
    .cont .form form input{
        width: 75%;
        padding: .4rem;
    }
    .cont .form form textarea{
        width: 75%;
        padding: .4rem;
        text-align: center;
    }
    .al .detalhe{
        width: 95%;
    }
    .detalhe .ende{
        display: block;
    }
    .detalhe div{
        width: 31%;
        height: 180px;
    }
    .detalhe div h1 svg{
      font-size: 2.5rem;
      padding: .5rem;
    }
    .detalhe div:hover{
        padding: .5rem;
    }
    .detalhe div p{
        font-size: 1rem;
    }   
}
@media (min-width:1024px){
    .centra h1{
        font-size: 3rem;
    }
    .centra h2{
        font-size: 2.4rem;
    }
    .centra h4{
        font-size: 1.3rem;
    }
    .centra button{
        width: 75%;
        padding: .1rem;
        margin-bottom: 3rem;
    }
    .cont{
    width: 65%;
    }
    .cont .form form span{
        font-size: 1.1rem;
    }
    .cont .form form input{
        width: 75%;
        padding: .4rem;
    }
    .cont .form form textarea{
        width: 75%;
        padding: .4rem;
        text-align: center;
    }
    .al .detalhe{
        width: 95%;
    }
    .detalhe div{
        width: 31%;
        height: 200px;
    }
    .detalhe div h1 svg{
      font-size: 3rem;
      padding: .5rem;
    }
    .detalhe div:hover{
        padding: .5rem;
    }
    .detalhe div p{
        font-size: 1rem;
    }  
}

