.back{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.274);
    z-index: 1000;
    }
    .back .cont{
        position: fixed;
        top: 3%;
        bottom: 3%;
        left:15%;
        right: 15%;
        background-color: rgba(240, 248, 255, 0.979);
        border-radius: 10px;
    }
    .back .cont img{
        width: 80%;
        background-size: cover;
    }
    .back .cont .fech{
        position: absolute;
        right: 2px;
        padding: .3rem;
        background-color: #f0f8ff00;
        font-size: 1.5rem;
    }
    .back .cont h3{
        font-size: 2rem;
        margin:0px;
        padding: 3px;
    }
    @media (min-width:200px){
        .back .cont{
            position: fixed;
            top:25%;
            bottom: 25%;
            left:2%;
            right: 2%;
        }
        .back .cont img{
            width: 95%;
            background-size: contain;
        }
        .back .cont h3{
            font-size: 1.3rem;
            margin:0px;
            padding: 3px;
        }
     }
