.posiçao{
    position: fixed;
    bottom: 5px;
    right: 3px;
    cursor: pointer;
}
.posiçao a{
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none;
    font-size: .9rem;
}
.posiçao a:hover{
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none;
    font-size: 1rem;
}
.posiçao a div{
    display: block;
    background-color: rgba(240, 248, 255, 0.514);
    color: black;
    padding: 2px;
    text-align: center;
    border-radius: 2px ;
}
.posiçao a div h3{
    color: black;
    text-align: center;
    font-size: 1rem;
    margin: 0px;
    padding: 0px;
}
.posiçao a div p{
  color: rgb(9, 168, 30);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.466);  
}
.posiçao svg{
 font-size: 2.3rem; 
 color: azure;  
 background-color: rgb(9, 168, 30);
 border-radius: 15%;
 box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.315);
 transition: .5s;
 margin-top: 8px;
}
@media (min-width:200px){
    .posiçao h3{
        font-size: .9rem;
    }
    .posiçao a{
        font-size: .7rem;
    }
    .posiçao a:hover{
        font-size: .8rem;
    }
    .posiçao a div{
        padding: 1px;
        border-radius: 1px ;
    }
    .posiçao svg{
     font-size: 1.7rem; 
     border-radius: 10%;
     margin-top: 8px;
    }
}
@media (min-width:480px){
    .posiçao h3{
        font-size: 1rem;
    }
    .posiçao a{
        font-size: .8rem;
    }
    .posiçao a:hover{
        font-size: .9rem;
    }
    .posiçao a div{
        padding: 1.5px;
        border-radius: 1.5px ;
    }
    .posiçao svg{
     font-size: 2rem; 
     border-radius: 11%;
     margin-top: 8px;
    }
}
@media (min-width:768px){
    .posiçao h3{
        font-size: 1.1rem;
    }
    .posiçao a{
        font-size: .9rem;
    }
    .posiçao a:hover{
        font-size: 1rem;
    }
    .posiçao a div{
        padding: 1.7px;
        border-radius: 1.7px ;
    }
    .posiçao svg{
     font-size: 2.4rem; 
     border-radius: 13%;
    }
}
@media (min-width:1024px){
    .posiçao h3{
        font-size: 1.2rem;
    }
    .posiçao svg{
     font-size: 2.6rem; 
    }
}