.nav{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-around;
    align-items: center;
    background-color: #ffffff;
    padding: 4px;
    padding-top: 60px;
}
nav p{
    padding: 0px;
    margin: 0px;
}
.nav img{
    width: 15%;
}
.nav img:hover{
    width: 17%;
}
.div{
    background-color: #C22D2D;
}
nav{
    position: fixed;
    background-color: #C22D2D;
    width: 100vw;
    display: flex;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
    padding-right: 4%;
}
ul h1{
    color: #fff;
    font-size: 1.5rem;
    cursor: pointer;
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 5px;
}
ul h1:hover{
    background-color: #fff;
    color:#C22D2D;
}

.contMenu{
    display: none;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.404);
    border-radius: 0px 0px 5px 0px;
    text-align: center;
    transition: 1s;
    margin-top: -5px;
}
ul:hover .contMenu{
    display: block; 
    transition: 1s;
 }
.contMenu li a{
    color: #ffffff;
    font-family: Arial, Helvetica, sans-serif;
}
.contMenu li a:hover{
    border-bottom: 1px solid white;
}
nav a{
    text-decoration: none;
    padding: 5px;
    margin-left: 15px;
    color: #fff;
    font-size: 1.3rem;
}
.active{
    color: #C22D2D;
    background-color: #ffffff;
    margin: 1px solid #C22D2D;
    padding-bottom: 2rem;
    padding-top: 1rem;
}
nav a:hover{
    color: #C22D2D;
    background-color: #ffffff;
    margin: 1px solid #C22D2D;
    padding-bottom: .5rem;
    padding-top: .3rem;
}
.serviços button{
    border: none;
    background-color: #ffffff21;
    color: #ffffff;
    padding: 8px;
    font-size: 1.5rem;
    z-index: 1000;
    cursor: pointer;
}
.serviços  a{
    background-color: rgba(255, 255, 255, 0);
    font-size: 1.1rem;
    cursor: pointer;
   z-index: 1000;
}
.serviços a:hover{
    background-color: rgba(0, 0, 0, 0);
    font-size: 1.2rem;
    color: #ffffff;
    padding: .5px;
    border-radius: 3px;
}

@media (min-width:200px){
    nav a{
        padding: 3px;
        margin-left: 12px;
        color: #fff;
        font-size: 1rem;
    }
    .nav img{
        width: 40%;
    }
    .nav img:hover{
        width: 41%;
    }
    .serviços button{
        padding: 3px;
        font-size: 1.2rem;
    }
    .serviços ul li{
        margin-bottom: 10px;
        text-align: left;
    }
    .serviços a{
        font-size: 1.1rem;
        padding: 2px;
        margin: 3px;
    }
    .serviços a:hover{
        font-size: 1.2rem;
    }
}
@media (min-width:480px){
    nav a{
        padding: 3px;
        margin-left: 13px;
        color: #fff;
        font-size: 1.1rem;
    }
    .nav img{
        width: 40%;
    }
    .nav img:hover{
        width: 41%;
    }
    .serviços button{
        padding: 3px;
        font-size: 1.3rem;
    }
    .serviços ul li{
        margin-bottom: 10px;
        text-align: left;
    }
    .serviços a{
        font-size: 1.2rem;
        padding: 3px;
    }
    .serviços a:hover{
        font-size: 1.3rem;
    }
}
@media (min-width:768px){ 
    nav a{
        padding: 4px;
        margin-left: 14px;
        color: #fff;
        font-size: 1.2rem;
    }
    .nav img{
        width: 30%;
    }
    .nav img:hover{
        width: 31%;
    }
    .serviços button{
        padding: 4px;
        font-size: 1.4rem;
    }
    .serviços ul li{
        margin-bottom: 10px;
        text-align: left;
    }
    .serviços a{
        font-size: 1.2rem;
        padding: 4px;
    }
    .serviços a:hover{
        font-size: 1.3rem;
    }
}
@media (min-width:1024px){   
    nav a{
        padding: 5px;
        margin-left: 15px;
        color: #fff;
        font-size: 1.3rem;
    } 
    .serviços button{
        padding: 2px;
        font-size: 1.3rem;
    }
    .serviços a{
        font-size: 1.1rem;
    }
    .serviços a:hover{
        font-size: 1.2rem;
    }
}
